/* eslint-disable no-unused-vars */
/* eslint-disable no-cond-assign */
/* eslint-disable no-useless-escape */
const ncname = '[a-zA-Z_][\\-\\.0-9_a-zA-Z]*'; // åŒ¹é…æ ‡ç­¾å
const qnameCapture = `((?:${ncname}\\:)?${ncname})`; // åŒ¹é…ç‰¹æ®Šæ ‡ç­¾
const startTagOpen = new RegExp(`^<${qnameCapture}`); // åŒ¹é…æ ‡ç­¾å¼€å§‹
const startTagClose = /^\s*(\/?)>/; // åŒ¹é…æ ‡ç­¾ç»“æŸ  >
const endTag = new RegExp(`^<\\/${qnameCapture}[^>]*>`); // åŒ¹é…æ ‡ç­¾ç»“å°¾
const attribute =
  /^\s*([^\s"'<>\/=]+)(?:\s*(=)\s*(?:"([^"]*)"+|'([^']*)'+|([^\s"'=<>`]+)))?/; // åŒ¹é…å±æ€§

// æ ¹èŠ‚ç‚¹ï¼Œå½“å‰çˆ¶èŠ‚ç‚¹
let root;
let currentParent;
//
const stack = [];
// å…ƒç´ ã€æ–‡æœ¬ nodeType å€¼
const ELEMENT_TYPE = 1;
const TEXT_TYPE = 3;

// ç”Ÿæˆast
function creatASTElement(tagName, attrs) {
  return {
    tag: tagName,
    type: ELEMENT_TYPE,
    children: [],
    attrs,
    parent: null,
  };
}

// å¤„ç†å¼€å§‹æ ‡ç­¾
function handleStartTag({ tagName, attrs }) {
  const element = creatASTElement(tagName, attrs);

  if (!root) {
    root = element;
  }

  currentParent = element;
  stack.push(element);
}

// å¤„ç†ç»“æŸæ ‡ç­¾
function handleEndTag(tagName) {
  // å–å‡ºæ ˆé¡¶å…ƒç´ 
  const element = stack.pop();

  currentParent = stack[stack.length - 1];

  if (currentParent) {
    element.parent = currentParent;
    currentParent.children.push(element);
  }
}

//  å°† HTML å­—ç¬¦ä¸²è½¬æ¢ä¸º ASTã€‚
export function parse(html) {
  while (html) {
    const textEnd = html.indexOf('<');

    if (textEnd === 0) {
      const startTagMatch = parseStartTag();
    }
  }

  // åŒ¹é…å¼€å§‹æ ‡ç­¾
  function parseStartTag() {
    const start = html.match(startTagOpen);

    if (start) {
      const match = {
        tagName: start[1],
        attrs: [],
      };

      // åŒ¹é…åˆ°åï¼Œæˆªå–æ‰å¼€å§‹æ ‡ç­¾
      advance(start[0].length);

      let end;
      let attr;

      while (
        !(end = html.match(startTagClose)) &&
        (attr = html.match(attribute))
      ) {
        advance(attr[0].length);
        attr = {
          name: attr[1],
          value: attr[3] || attr[4] || attr[5],
        };
        match.attrs.push(attr);
      }

      if (end) {
        advance(1);
        return match;
      }
    }
  }

  function advance(n) {
    html = html.substring(n);
  }

  return root;
}
